.menu-btn {
  width: 22px;
  height: 16px;
  z-index: 2;
  position: relative;
  overflow: hidden;
}

.menu-btn span {
  width: 18px;
  height: 1px;
  background-color: #fff;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menu-btn span:nth-of-type(2) {
  top: calc(50% - 5px);
}

.menu-btn span:nth-of-type(3) {
  top: calc(50% + 5px);
}

.menu {
  width: 100%;
  height: 100%;
  background: #191919;
  justify-content: center;
  padding-top: 58px;
  padding-left: 20px;
  transition: transform .5s;
  display: flex;
  position: fixed;
  top: 48px;
  left: 0;
  transform: translateY(-150%);
}

.menu.active {
  transform: translateY(0);
}

.menu li {
  list-style-type: none;
}

.menu-btn.active span:nth-of-type(1) {
  display: none;
}

.menu-btn.active span:nth-of-type(2) {
  top: 50%;
  transform: translate(-50%)rotate(45deg);
}

.menu-btn.active span:nth-of-type(3) {
  top: 50%;
  transform: translate(-50%)rotate(-45deg);
}

.menu__nav {
  width: 452px;
}

@media screen and (min-width: 768px) {
  .menu {
    height: auto;
    padding-bottom: 100px;
    box-shadow: 0 20px 80px #191919;
  }

  .menu__nav {
    width: 610px;
  }
}

@media screen and (min-width: 1440px) {
  .menu, .menu-btn {
    display: none;
  }
}

